<template>
  <v-dialog max-width="600px">
    <v-btn slot="activator" color="blue" dark>
      <v-icon left>add</v-icon>
      Tambah project baru
    </v-btn>
    <v-card class="pa-3">
      <v-card-title>
        <h2>Form tambah project</h2>
      </v-card-title>
      <v-card-text>
        <v-form>
          <v-text-field label="Nama project" prepend-icon="folder" v-model="nama"></v-text-field>
          <v-textarea label="Isi" v-model="isi"></v-textarea>
          <v-menu :close-on-content-click="false">
            <v-text-field :value="isiTgl" slot="activator" prepend-icon="date_range" label="Tanggal"></v-text-field>
            <v-date-picker v-model="tgl" scrollable></v-date-picker>
          </v-menu>
          <v-spacer></v-spacer>
          <v-btn @click="kirim">
            TAMBAH
            <v-icon right>add</v-icon>
          </v-btn>
        </v-form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>

import format from 'date-fns/format'

export default {
  data(){
    return{
      nama:'',
      isi:'',
      tgl:null
    }
  },
  methods:{
    kirim(){
      console.log(this.nama + '|' + this.isi);
    }
  },
  computed:{
    isiTgl(){
      return this.tgl ? format(this.tgl, 'DD MMM YYYY') : ''
    }
  }
}
</script>